import axios from 'axios';

// Creeaz o instan axios pentru a gestiona cererile API
export const userTransactionsApi = axios.create({
  baseURL: 'https://wallet.b.goit.study', // Seteaz base URL doar o dat
});

// Funcie pentru setarea tokenului n antetul de autorizare
export const setToken = token => {
  userTransactionsApi.defaults.headers.common.Authorization = `Bearer ${token}`;
};

// Funcie pentru a elimina tokenul din antetul de autorizare
export const removeToken = () => {
  delete userTransactionsApi.defaults.headers.common.Authorization;
};
